.gym {
  padding-top: 120px;
  padding-bottom: 235px;

  @include vp-1199 {
    padding-top: 90px;
    padding-bottom: 183px;
  }

  @include vp-767 {
    padding-top: 55px;
    padding-bottom: 72px;
  }
}

.gym__container {
  display: flex;

  @include vp-1199 {
    flex-direction: column;
    align-items: center;
  }
}

.gym__container-inner {
  margin-left: auto;
  margin-right: auto;
  padding-top: 30px;
  padding-right: 16px;
  width: 33%;

  @include vp-1199 {
    width: 75%;
    margin: 0;
    padding: 0;
  }

  @include vp-767 {
    width: 100%;
  }
}

.gym h2 {
  font-family: $ff-alumni-sans;
  font-size: 48px;
  font-weight: 600;
  line-height: 58px;
  letter-spacing: 0.03em;
  margin-top: 10px;
  margin-bottom: 6px;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50px;
    text-align: center;
    margin-bottom: 0;
    margin-left: -14px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 38px;
    text-align: start;
    margin-bottom: 5px;
    margin-left: 0;
  }
}

.gym span {
  display: block;
  font-size: 18px;
  font-weight: 400;
  line-height: 27px;
  margin-bottom: 38px;

  @include vp-1199 {
    margin-bottom: 33px;
    text-align: center;
  }

  @include vp-767 {
    margin-bottom: 23px;
    text-align: start;
  }
}

.gym p {
  margin: 0;

  @include vp-1199 {
    text-align: center;

    &:last-of-type {
      display: none;
    }
  }

  @include vp-767 {
    text-align: start;
  }
}

.gym p:not(:last-of-type) {
  margin-bottom: 8px;

  @include vp-1199 {
    margin-bottom: 43px;
  }

  @include vp-767 {
    margin-bottom: 33px;
  }
}

.gym picture,
.gym__image {
  @include vp-1199 {
    width: 100%;
  }
}

.gym__image {
  aspect-ratio: 660 / 401;
  background-color: #afafaf;

  &--tablet,
  &--mobile {
    display: none;
  }

  @include vp-1199 {
    aspect-ratio: 678 / 345;

    &--mobile,
    &--desktop {
      display: none;
    }

    &--tablet {
      display: block;
    }
  }

  @include vp-767 {
    aspect-ratio: 320 / 313;
    margin-bottom: 40px;

    &--desktop,
    &--tablet {
      display: none;
    }

    &--mobile {
      display: block;
    }
  }
}

.gym__container-visual {
  position: relative;
  width: 57%;

  @include vp-1199 {
    width: 100%;
  }

  @include vp-767 {
    width: 100vw;
  }
}

.gym__video {
  position: absolute;
  display: block;
  left: -10%;
  transform: translateY(-50%);
  width: 364px;
  height: 228px;

  &--nojs {
    display: none;
  }

  @include vp-1199 {
    width: 39%;
    left: 50%;
    height: auto;
    transform: translate(-50%, -50%);
    aspect-ratio: 266 / 160;
  }

  @include vp-767 {
    position: relative;
    bottom: 0;
    left: 0;
    transform: none;
    width: 100%;
    height: auto;
    aspect-ratio: 320 / 161;
  }
}

.gym__video-placeholder {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-image: url("../img/gym-video-poster-desktop.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #14367a80;

  @include retina {
    background-image: url("../img/gym-video-poster-desktop@2x.jpg");
  }

  @include vp-767 {
    background-image: url("../img/gym-video-poster-mobile.jpg");

    @include retina {
      background-image: url("../img/gym-video-poster-mobile@2x.jpg");
    }
  }
}

.gym__video-button {
  border: none;
  background: none;
  padding: 0;
  width: 100px;
  height: 100px;
  cursor: pointer;

  @include hover-focus {
    opacity: 0.8;

    @include active {
      opacity: 0.6;
    }
  }

  @include vp-1199 {
    width: 73px;
    height: 73px;

    svg {
      width: 73px;
      height: 73px;
    }
  }
}

.gym__video-player {
  width: 100%;
  height: 100%;
  border: none;
}
